 .checkbox {
   display: inline-flex;
   cursor: pointer;
   position: relative;
}

.checkbox > span {
   color: #B2B2B2;
   padding: 0.5rem 0.25rem;
   width: 412px;
}

.checkbox > input {
   height: 22px;
   width: 22px;
   -webkit-appearance: none;
   -moz-appearance: none;
   -o-appearance: none;
   appearance: none;
   outline: none;
   transition-duration: 0.3s;
   background: #FFFFFF 0% 0% no-repeat padding-box;
   cursor: pointer;
   opacity: 1;
   border: 1px solid #95989A;
   border-radius: 4px;
   opacity: 1;
   position: absolute;
   top: 10px;
 }

.checkbox > input:checked + span::before {
   content: '\2713';
   display: block;
   text-align: center;
   background: transparent url(../images/Checkmark.png) 0% 0% no-repeat padding-box;
   opacity: 1;
   position: absolute;
   left: -25px;
   top: 13px;
   border: none;

   
}

